<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <div class="row">
      <form @submit.prevent="onUpdateEmployee" class="co1 s12">
        <div class="row">
          <div class="input-field co1 s12">
            <input disabled type="text" v-model="employee_id" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="name" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="dept" required>
          </div>
        </div>
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="position" required>
          </div>
        </div>
        <button type="submit" class="btn info">更新</button>
        <router-link to="/" class="btn grey">取消</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(data => {
      data.forEach(doc => {
        console.log(doc.data())
        next(vm => {
          vm.employee_id = doc.data().employee_id
          vm.name = doc.data().name
          vm.dept = doc.data().dept
          vm.position = doc.data().position
        })
      })
    }).catch(error => {
      console.log(error)
    })
  },
  watch: {
    '$router': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('employee_id', '==', this.$router.params.employee_id).get().then(data => {
        data.forEach(doc => {
          const da = doc.data()
          console.log(da)
          this.name = da.name
          this.dept = da.dept
          this.employee_id = da.employee_id
          this.position = da.position
        })
      }).catch(error => {
        console.log(error)
      })
    },
    onUpdateEmployee () {
      const da = {
        employee_id: this.employee_id,
        name: this.name,
        dept: this.dept,
        position: this.position
      }
      db.collection('employees').where('employee_id', '==', this.employee_id).get().then(data => {
        data.forEach(doc => {
          doc.ref.update(da).then(() => {
            this.$router.push({name: 'view-employee', params: {employee_id: this.employee_id}})
          }).catch(error => {
            console.log(error)
          })
        })
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>



