<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" :key="employee.id" class="collection-item">
        <div class="chip">{{ employee.dept }}
          </div>
          {{ employee.employee_id}}:{{ employee.name }}
        <router-link class="secondary-content" :to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link class="btn-floating btn-large red" to="/new">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
    return {
      employees: []
    }
  },
  created () {
    db.collection('employees').orderBy('dept').get().then(data => {
      // console.log(data)
      data.forEach(doc => {
        const da = doc.data()
        const daa = {
          id: doc.id,
          name: da.name,
          dept: da.dept,
          employee_id: da.employee_id,
          position: da.position
        }
        console.log(daa)
        this.employees.push(daa)
      })
    }).catch(error => {
      console.log(error)
    })
  }
}
</script>



