<template>
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="onSaveEmployee" class="co1 s12">
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="employee_id" required>
            <label >Employee ID#</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="name" required>
            <label >Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="dept" required>
            <label >Department</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field co1 s12">
            <input type="text" v-model="position" required>
            <label >Position</label>
          </div>
        </div>
        <button type="submit" class="btn info">保存</button>
        <router-link to="/" class="btn grey">取消</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-employee',
  data () {
    return {
      employee_id: '',
      name: '',
      dept: '',
      position: ''
    }
  },
  methods: {
    onSaveEmployee () {
      const da = {
        employee_id: this.employee_id,
        name: this.name,
        dept: this.dept,
        position: this.position
      }
      db.collection('employees').add(da).then(data => {
        this.$router.push('/')
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>



