<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{ name }}</h4>
      </li>
      <li class="collection-item">
        Employee ID:{{ employee_id }}
      </li>
      <li class="collection-item">
        Department:{{ dept }}
      </li>
      <li class="collection-item">
        Position:{{ position }}
      </li>
      <router-link to="/" class="btn grey">Back</router-link>
      <button @click="deleteEmployee" class="btn red">Delete</button>
      <div class="fixed-action-btn">
      <router-link class="btn-floating btn-large red" :to="{name: 'edit-employee', params:{employee_id: employee_id}}">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
    </ul>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-employee',
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(data => {
      data.forEach(doc => {
        console.log(doc.data())
        next(vm => {
          vm.employee_id = doc.data().employee_id
          vm.name = doc.data().name
          vm.dept = doc.data().dept
          vm.position = doc.data().position
        })
      })
    }).catch(error => {
      console.log(error)
    })
  },
  watch: {
    '$router': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('employee_id', '==', this.$router.params.employee_id).get().then(data => {
        data.forEach(doc => {
          const da = doc.data()
          console.log(da)
          this.name = da.name
          this.dept = da.dept
          this.employee_id = da.employee_id
          this.position = da.position
        })
      }).catch(error => {
        console.log(error)
      })
    },
    deleteEmployee () {
      if (confirm('确定删除？')) {
        db.collection('employees').where('employee_id', '==', this.employee_id).get().then(data => {
          data.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/')
          })
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
}
</script>



